export class CalcEmail {

    _id: string =    '';
    app: string = '';
    type: string = 'Finish';
    email: string = 'team@videoagency.com';
    subject: string = 'Your Video Production Estimate';
    message: string = `
      <p>Hello!</p>
      <p> Thank you for using our video production cost calculator. Just so you know, your estimate came to approximately $40,000 (and $30,000 if you go with a slightly lower production quality). </p>
      <p> If you have any further questions, feel free email us :) </p>
      <p> Thank You</p>`;
    sendEmail: boolean = false;

    constructor(data: any) {
        let self: any = this;
        for (let prop in data){
             self[prop] = data[prop] || self[prop];
        }
    }
}



